<template>
  <div id="app">
    <b-alert
      variant="danger"
      dismissible
      fade
      :show="error"
      @dismissed="error = false"
    >
      {{ errorMessage }}
    </b-alert>
    <form v-if="hasGroupData" @submit.prevent="submitForm">
      <div v-for="group in groupData" :key="group.name" class="form-group row">
        <label :for="group.name" class="col-md-2 col-form-label">
          <p v-if="group.type !== 'checkbox'">
            {{ group.label }}
          </p>
        </label>
        <div class="col-md-10">
          <template v-if="group.type === 'password'">
            <input
              :id="group.name"
              v-model.trim="group.value"
              class="form-control input-password"
              :type="group.type"
              :required="group.required"
              :name="group.name"
            />
            <a href="javascript:void(0)" class="show-pass"
              ><i class="fa fa-eye-slash"></i
            ></a>
          </template>
          <template v-if="group.type === 'text'">
            <input
              :id="group.name"
              v-model.trim="group.value"
              class="form-control"
              :type="group.type"
              :required="group.required"
              :name="group.name"
            />
          </template>
          <template v-if="group.type === 'textarea'">
            <textarea
              :id="group.name"
              v-model.trim="group.value"
              class="form-control"
              :required="group.required"
              :name="group.name"
            ></textarea>
          </template>
          <template v-if="group.type === 'checkbox'">
            <b-form-checkbox
              :id="group.name"
              v-model.lazy="group.value"
              :name="group.name"
            >
              {{ group.label }}
            </b-form-checkbox>
          </template>
          <template v-if="group.type === 'select'">
            <multiselect
              v-model.lazy="group.selectedValue"
              :options="group.options"
              :searchable="false"
              :show-labels="false"
              label="label"
              track-by="label"
            ></multiselect>
          </template>
          <template v-if="group.type === 'selectsearch'">
            <multiselect
              v-model.lazy="group.value"
              :options="group.options"
              :custom-label="nameWithLang"
              label="label"
              track-by="label"
            ></multiselect>
          </template>
          <template v-if="group.type === 'multiselect'"
            ><multiselect
              v-model="group.selectedValues"
              :options="group.options"
              :multiple="true"
              :close-on-select="false"
              :preserve-search="true"
              label="label"
              track-by="label"
              :reset-after="false"
              group-values="groupOptions"
              group-label="groupLabel"
              :group-select="true"
              :hide-selected="true"
            ></multiselect>
          </template>
        </div>
      </div>
      <div class="mb-3 row">
        <label class="col-md-2 col-form-label">&nbsp;</label>
        <div class="col-md-10">
          <button
            class="btn btn-primary pmt-2"
            type="submit"
            @click.prevent="submitForm"
          >
            Запиши
          </button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      groupData: [],
      error: false,
      errorMessage: '',
    }
  },
  computed: {
    hasGroupData() {
      return this.groupData.length !== 0
    },
  },
  mounted() {
    this.getData()
  },
  methods: {
    async getData() {
      const apiUrl = !this.$lodash.isEmpty(window.jsonData)
        ? window.jsonData
        : {
            jsonUrlControls:
              'https://corporate.orak365.net/SmartRegistry/UserGroups/UserGroupData',
          }

      try {
        const { data: groupData } = await this.axios.get(apiUrl.jsonUrlControls)
        console.log('getData', groupData)
        this.groupData = groupData
      } catch (e) {
        console.error('Error loading data from the server:', e)
        this.error = true
        this.errorMessage = e.errorMessage
        setTimeout(() => this.resetError(), 5000)
      }
    },
    async submitForm() {
      try {
        this.resetError()
        console.log('submit', this.groupData)

        const apiUrl = !this.$lodash.isEmpty(window.jsonData)
          ? window.jsonData
          : {}

        const { data } = await this.axios.post(
          apiUrl.jsonUrlCreateEdit,
          this.groupData
        )

        if (data.status === 'Error' || data.status === 'error') {
          this.error = true
          this.errorMessage = data.errorMessage
          setTimeout(() => this.resetError(), 5000)
        } else if (data.status === 'Success' || data.status === 'success') {
          location.assign(data.redirectUrl)
        }
      } catch (e) {
        console.error(e)
        console.log('Error submitting form', '\n', e)
        if (!this.$lodash.isEmpty(e.errorMessage)) {
          this.error = true
          this.errorMessage = e.errorMessage
        }
        setTimeout(() => this.resetError(), 5000)
      }
    },
    resetError() {
      this.error = false
      this.errorMessage = ''
    },
  },
}
</script>
